import { useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';

import Button from 'components/Button/Button';
import { Title, UsersPage } from './styles';

function Users() {
  // useNavigate  ,         url
  const navigate = useNavigate();

  const goToAboutPage = () => {
    navigate('/about');
  };

  // * useEffect    users
  //   
  //  ..           Interval
  useEffect(() => {
    return () => {
      console.log('  Users unmounting');
    };
  }, []);

  //   Users    ,       
  //    
  const usersData = [
    { id: 1, userName: 'User 1' },
    { id: 2, userName: 'User 2' },
    { id: 3, userName: 'User 3' },
  ];

  //       map
  const usersLinks = usersData.map((userData)=>{
    return <Link to={`/users/${userData.id}`}>{userData.userName}</Link>
  })

  return (
    <UsersPage>
      {/* Content UsersPage */}
      <Title>Select user</Title>
      {/*  ,              id */}
      {/* <Link to='/users/1'>User 1</Link>
      <Link to='/users/2'>User 2</Link>
      <Link to='/users/3'>User 3</Link> */}
      {usersLinks}
      <Button name='Go to About Page' onClick={goToAboutPage} />
    </UsersPage>
  );
}

export default Users;
